<template>
    <div class="charter">
        <div class="auto">
            <breadcrumb />
            <h2 class="title">包車服務</h2>
            <el-form ref="form" label-width="80px" :label-position="labelPosition">
                <el-form-item label="訂車日期">
                    <el-date-picker v-model="date" type="date" placeholder="選擇日期"></el-date-picker>
                </el-form-item>
                <el-form-item label="訂車時間">
                    <el-time-picker v-model="time" placeholder="選擇時間"></el-time-picker>
                </el-form-item>
                <el-form-item label="預約車型">
                    <el-select v-model="seat" placeholder="選擇車型" class="input">
                        <el-option label="5人座" value="5"></el-option>
                        <el-option label="7人座" value="7"></el-option>
                        <el-option label="9人座" value="9"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="車款">
                    <el-select v-model="style" placeholder="選擇車型" class="input">
                        <el-option label="一般" value="general"></el-option>
                        <el-option label="進口" value="import"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="車款">
                    <el-checkbox-group v-model="sign">
                        <el-checkbox label="代客舉牌" name="type"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item label="安全座椅">
                    <el-select v-model="safe" placeholder="選擇數量" class="input">
                        <el-option label="0" value="0"></el-option>
                        <el-option label="1" value="1"></el-option>
                        <el-option label="2" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="行程天數">
                    <el-select v-model="days" placeholder="選擇天數" class="input">
                        <el-option v-for="i in 15" :label="i" :value="i" :key="i"></el-option>
                    </el-select>
                </el-form-item>
                <div class="section">
                    <h3>景點選擇使用方式</h3>
                    <p>1.請依照天數順序，點擊行程表右上方「編輯景點」按鈕，以開始選擇景點。</p>
                    <p>2.請依行程順序，依序點擊想去的景點。</p>
                    <p>3.可自行輸入景點 (輸入後請使用google關鍵字點選)</p>
                    <p>4.因每日時間有限，故僅提供6個景點選擇。每日起點為前一日的最後景點。</p>
                    <p>5.試算後的行車時間請勿超過六小時，避免造成行程無法跑完。</p>
                    <p>6.如有兩天以上，請在最後一個景點填上住宿地點。</p>
                </div>
                <el-form-item>
                    <el-button type="success" @click="submit" class="btn">試算車費</el-button>
                    <router-link to="/order" class="btn">
                        <el-button type="info">重選服務</el-button>
                    </router-link>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import breadcrumb from '@/components/breadcrumb/index.vue';
import { mapGetters } from 'vuex';
export default {
    name: 'charter',
    components: {
        breadcrumb
    },
    computed: {
        ...mapGetters(['labelPosition']),
        date: {
            get() {
                return this.$store.state.charter.date;
            },
            set(val) {
                this.$store.commit('CHARTERDATE', val);
            }
        },
        time: {
            get() {
                return this.$store.state.charter.time;
            },
            set(val) {
                this.$store.commit('CHARTERTIME', val);
            }
        },
        seat: {
            get() {
                return this.$store.state.charter.seat;
            },
            set(val) {
                this.$store.commit('CHARTERSEAT', val);
            }
        },
        style: {
            get() {
                return this.$store.state.charter.style;
            },
            set(val) {
                this.$store.commit('CHARTERSTYLE', val);
            }
        },
        sign: {
            get() {
                return this.$store.state.charter.sign;
            },
            set(val) {
                this.$store.commit('CHARTERSIGN', val);
            }
        },
        safe: {
            get() {
                return this.$store.state.charter.safe;
            },
            set(val) {
                this.$store.commit('CHARTERSAFE', val);
            }
        },
        days: {
            get() {
                return this.$store.state.charter.days;
            },
            set(val) {
                this.$store.commit('CHARTERDAYS', val);
            }
        }
    },
    methods: {
        submit() {},
        toPage(url) {
            this.$store.dispatch('toPage', url);
        }
    }
};
</script>
<style lang="scss" scoped>
.charter {
    font-family: '微軟正黑體', sans-serif;
    background-color: #f1f1f1;
    padding: 0 0 40px 0;
    color: $text;
    .auto {
        width: 1440px;
        margin: auto;
        display: flex;
        flex-direction: column;
        @media screen and (max-width: 1440px) {
            width: 1224px;
        }
        @media screen and (max-width: 1224px) {
            width: 96%;
        }
        h2 {
            color: #999;
            font-size: 24px;
            line-height: 40px;
            font-weight: 700;
            padding: 0 0 0 16px;
            margin: 8px;
            position: relative;
            &:before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                margin: auto;
                background-color: #999;
                width: 4px;
                height: 24px;
            }
        }
        form {
            width: 800px;
            padding: 16px;
            margin: 0 0 40px 0;
            background: #f7f7f7;
            align-self: center;
            font-size: 12px;
            @media screen and (max-width: 1224px) {
                width: 96%;
            }
            .subTitle {
                font-size: 16px;
                line-height: 32px;
                font-weight: 600;
            }
            .section {
                margin: 0 0 16px 0;
                h3 {
                    color: $title;
                    font-size: 16px;
                    line-height: 32px;
                }
                p {
                    line-height: 24px;
                }
            }
            a.btn {
                color: white;
                margin: 0 0 0 16px;
            }
        }
    }
}
</style>